@font-face {
  font-family: Poppins;

  font-weight: 400;

  src: url(/portfolio/static/media/Poppins-Bold.404e299b.ttf) format("ttf");
}

body {
  --tw-gradient-to: #ec4899;
  --tw-gradient-from: #8b5cf6;
  --tw-gradient-stops: var(--tw-gradient-from), var(--tw-gradient-to, rgba(139, 92, 246, 0));
  background-image: linear-gradient(to right, var(--tw-gradient-stops))
}

body {
  color : #FAFAFA;
  font-family: "Montserrat", sans-serif;
}

nav {
  background : transparent;
  width : 100%;
  height : 90px;
  padding: 0;
}

nav ul {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

nav ul li {
  margin: 20px;
  padding : 10px;
}

nav ul li a {
  color : black;
  font-size: 24px;
}

.menu {
  top : 80px;
  right : 440px;
}

.about {
  height: 450px;
  width: 1000px;
  background-color: cyan;
  color: black;
  border-radius: 10px;
  align-content: center;
  text-align: center;
  padding: 20px;
  margin: 30px;
  font-size: 25px;
}

input {
  width : 320px;
  height : 60px;
  border: 2px solid black;
  padding: 5px;
  text-align: center;
  background: white;
  border-radius: 8px;
}

input:focus {
  outline: none;
}

button {
  width : 210px;
  height : 60px;
  background-color: rgb(241, 0, 241);
  border-radius: 5px;
  border: 2px solid rgb(241, 0, 241);
  transition: 300ms all;
}

.abilitiescontainer {
  background-color: cyan;
  height: 200px;
  width: 1200px;
  color : black;
  font-size: 30px;
}

.abilities {
  font-size: 23px;
}

@media all and (max-width : 600px){
  html {
    zoom : 70%;
  }

  .menu {
    position: absolute;
    top : 100px;
    right : 175px;
  }

  .about {
    width : 500px;
    height: 900px;
  }

  .abilitiescontainer {
    width: 600px;
    height : 500px;
  }

  .abilities {
    display : flex;
    flex-direction: column;
    text-align: center;
  }

  .news-register {
    display: flex;
    flex-direction: column;
  }
}

.ease-in{
  transition-timing-function: cubic-bezier(0.4, 0, 1, 1);
}

.duration-100{
  transition-duration: 100ms;
}

.transition-all{
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
}

